body {
  text-align: center;
  background-color: #011F3F;
}

#level-title {
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 3rem;
  margin:  5%;
  color: #FEF2BF;
}


.container {
  display: block;
  width: 50%;
  margin: auto;

}

.btn {
  margin: 25px;
  display: inline-block;
  height: 200px;
  width: 200px;
  border: 10px solid black;
  border-radius: 20%;
}

.game-over {
  background-color: red;
  opacity: 0.8;
}

.red {
  background-color: red;
}

.green {
  background-color: green;
}

.blue {
  background-color: blue;
}

.yellow {
  background-color: yellow;
}

.pressed {
  box-shadow: 0 0 20px white;
  background-color: grey;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: rgb(7, 83, 163);
  color: white;
  text-align: center;
}

a {
  text-decoration: none;
}